.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 20px;
  gap: 30px;
  background-color: var(--page-background-color2);

  .title {
    font-family: 'JuliusSansOne';
    color: var(--point-color);
    font-size: 0.8rem;
    font-weight: 700;
  }

  .subTitle {
    font-family: 'GowunDodum';
  }

  .division {
    width: 100%;
    height: 0;
    border-top: 1px dashed var(--point-color);
  }

  .commentsDesc {
    font-family: 'GowunDodum';
    text-align: center;
  }

  .commentForm {
    display: flex;
    flex-direction: column;
    gap: 8px;
    width: 100%;
    font-family: 'GowunDodum';
    background-color: white;
    padding: 8px 16px 20px;
    position: relative;

    .line {
      display: flex;
      flex-direction: column;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      width: 100%;
      z-index: 1;
      padding: 5px 20px;

      > span {
        width: 100%;
        border-bottom: 1px solid var(--gray-color2);
        height: 45px;

        &:nth-child(3),
        &:nth-child(4),
        &:nth-child(5) {
          height: 25px;
        }
      }
    }

    .row {
      display: flex;
      z-index: 2;

      > div:nth-child(1) {
        flex: none;
        width: 70px;
        font-size: 0.8rem;
        height: 100%;

        > span {
          width: 100%;
          height: 40px;
          display: flex;
          align-items: center;
          justify-content: space-between;

          &::after {
            content: '';
            border-right: 1px solid var(--gray-color2);
            width: 1px;
            height: 10px;
            background-color: var(--gray-color2);
          }
        }
      }

      > div:nth-child(2) {
        flex: 1;

        > section {
          flex: 1;
          display: flex;
          width: 100%;
          padding: 0 12px;
          margin: 10px 0 0;
        }

        .input {
          min-height: 40px;
          flex: 1;
          border: none;
          padding: 10px 12px;
          font-size: 0.8rem;
          font-family: 'GowunDodum';
          background-color: unset;
        }

        .textarea {
          font-family: 'GowunDodum';
          flex: 1;
          border: none;
          font-size: 0.8rem;
          line-height: 190%;
          height: 100px;
          resize: none;
          overflow: hidden;
          background-color: unset;
        }
      }
    }

    .errorText {
      color: #fd6666;
      font-size: 0.8rem;
    }

    &.large {
      .line {
        > span {
          &:nth-child(3),
          &:nth-child(4),
          &:nth-child(5) {
            height: 28px;
          }
        }
      }
      .row {
        section {
          margin-top: 12px !important;
        }
        .textarea {
          height: 110px;
        }
      }
    }

    button {
      margin-top: 8px;
      font-family: 'GowunDodum';
      appearance: none;
      border: 0;
      cursor: pointer;
      border-radius: 4px;
      padding: 6px 12px;
      font-weight: 800;
      font-size: 0.8rem;
      letter-spacing: -0.2px;
      transition: transform 120ms ease, filter 120ms ease, opacity 120ms ease;
      border: 1px solid var(--point-color2);
      background-color: var(--page-background-color);
    }
  }

  .commentList {
    margin-top: 14px;
    display: grid;
    gap: 10px;
    font-family: 'GowunDodum';
    width: 100%;

    .commentItem {
      border: 1px solid var(--gray-color2);
      border-radius: 12px;
      padding: 12px;
      background-color: var(--page-background-color);

      .commentHead {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }

      .commentName {
        font-weight: 800;
      }

      .commentDate {
        font-size: 0.7rem;
        color: rgba(26, 26, 34, 0.45);
        letter-spacing: -0.1px;
      }

      .commentMsg {
        margin-top: 6px;
        white-space: pre-wrap;
        word-break: break-word;
        color: var(--gray-color3);
      }

      .delBtn {
        display: flex;
        justify-content: flex-end;

        button {
          border: 1px solid #ddd;
          background: white;
          border-radius: 10px;
          padding: 6px 10px;
          cursor: pointer;
          font-size: 0.7rem;
        }
      }
    }
  }
}
